import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {ConfigProvider} from "../../providers/config/config";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";


@IonicPage()
@Component({
  selector: 'page-fund-transer-other-bank04',
  templateUrl: 'fund-transer-other-bank04.html',
})
export class FundTranserOtherBank04Page {
// This variable is for parameters
  accountNo;
  amount;
  srcAccDetails;
  selectedDest;
  fundTransferType;
  receiverType;
  initObTxDetails;

  //this variable for show hide
  routeShow;

  //this value is for form input
  otherBankTransferError04 = "";
  formVal = {tpin: "", otp: ""};
  tpin = "";
  otp = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public ftOtherBankService: FundTransferOtherBankProvider,
              public tokenService: TokenServiceProvider,
              public config: ConfigProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.amount = this.navParams.get("amount");
    this.srcAccDetails = JSON.parse(this.navParams.get("srcAccDetails"));
    this.selectedDest = JSON.parse(this.navParams.get("selectedDest"));
    this.initObTxDetails = JSON.parse(this.navParams.get("initObTxDetails"));
    this.fundTransferType = this.navParams.get("fundTransferType");
    this.receiverType = this.navParams.get("receiverType");

    this.routeShow = this.fundTransferType != config.transferTypeNPSB;
  }

  iTransferOtherBank04Action = function () {
    let flag = true;
    this.tpin = this.formVal.tpin;
    this.otp = this.formVal.otp;

    let validateTpinExp = new RegExp("[0-9]{4}$");
    let validateOtpExp = new RegExp("[0-9]{6}$");


    if (this.tpin == "" || typeof(this.tpin) == "undefined") {
      this.otherBankTransferError04 = "Please Enter TPIN.";
      flag = false;
      return;
    }

    if (this.otp == "" || typeof(this.otp) == "undefined") {
      this.otherBankTransferError04 = "Please Enter OTP.";
      flag = false;
      return;
    }

    if (!validateTpinExp.test(this.tpin)) {
      this.otherBankTransferError04 = "Invalid TPIN."
      flag = false;
      this.formVal = {tpin: ""};
      return;
    }

    if (!validateOtpExp.test(this.otp)) {
      this.otherBankTransferError04 = "Invalid OTP."
      flag = false;
      this.formVal = {otp: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }

  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encTxID = this.cryptoUtil.getEncryptVal(this.initObTxDetails.transferId);
      let encTpin = this.cryptoUtil.getEncryptVal(this.tpin);
      let encOtp = this.cryptoUtil.getEncryptVal(this.otp);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.ftOtherBankService.confirmOtherBankTransfer(unMaskToken, appData.userId, appData.appId, encTxID, encTpin, encOtp)
        .then(data => {
          this.onConfirmFundTransferComplete(data);
        });
    }
  };

  onConfirmFundTransferComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('FundTranserOtherBank05Page', {
        'initObTxDetails': JSON.stringify(this.initObTxDetails),
        'selectedDest': JSON.stringify(this.selectedDest),
        'fundTransferType': this.fundTransferType
      });
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError04 = data.note;
      this.formVal = {tpin: "", otp: ""};
    }

    else {
      this.otherBankTransferError04 = "System Error Contact with Administrator.";
      this.formVal = {tpin: "", otp: ""};
    }
  };

  ionViewDidLoad() {

  }

}



